<template>
	<div id="loading">
		<div class="circle-wrap">
			<img src="static/img/loading_circle.png" alt="">
		</div>
		<div class="logo-wrap">
			<img src="static/img/loading_logo.png" alt="">
		</div>
	</div>
</template>

<script>
	export default {
		data(){
			return{
			}
		},
		mounted(){

		}
	}
</script>

<style scoped>
	@keyframes spin {
		from {
			transform: rotate(0deg);
		}
		to {
			transform: rotate(360deg);
		}
	}
	@-webkit-keyframes spin {
		from {
			-webkit-transform: rotate(0deg);
		}
		to {
			-webkit-transform: rotate(360deg);
		}
	}
	#loading{
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		z-index: 100000000;
		background-color: #fff;
	}
	#loading img{
		width: 100%;
		height: 100%
	}
	#loading .circle-wrap{
		position: fixed;
		top: 50%;
		left: 50%;
		width: 50px;
		height: 50px;
		margin: -25px 0 0 -25px;
		animation: spin 1s linear infinite;
		-webkit-animation: spin 1s linear infinite;
	}
	#loading .logo-wrap{
		position: fixed;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 60px;
		height: 60px;
	}
</style>



