import util from '@/config/util'
var cloudApp = {
	init: function() {
		let jpush_relation = util.getStorage('jpush_relation');
		if(jpush_relation && util.isCloud()) {
			setTimeout(function(){
				cloudApp.relation = jpush_relation;
				cloudApp.checkconnect();
				cloudApp.initAjpush();
				cloudApp.checkSmartUpdate();
			}, 1000);
		}
	},

	checkSmartUpdate: function() {
		api.addEventListener({
			name:'smartupdatefinish'
		}, function(ret, err){
			api.rebootApp();
		});
	},

	checkconnect: function() {
		api.addEventListener({
			name:'offline'
		}, function(ret, err){
			api.openFrame({
				name: 'frm_connect',
				url: 'widget://html/connect.html',
				bounces: true,
				rect: {
					x: 0,
					y: 0,
					w: 'auto',
					h: 'auto'
				}
			});
			return false;
		});

		if(api.connectType == 'none') {
			api.openFrame({
				name: 'frm_connect',
				url: 'widget://html/connect.html',
				bounces: true,
				rect: {
					x: 0,
					y: 0,
					w: 'auto',
					h: 'auto'
				}
			});
			return false;
		}

		api.addEventListener({
			name:'online'
		}, function(ret, err){
			api.closeFrame({
				name: 'frm_connect'
			});
			var url_cn = cloudApp.igetPrefs('url_cn');
			if(url_cn) {
				window.location.href = url_cn;
			}
		});
	},

	netAudioPlay: function(resource, times) {
		var netAudio = api.require('netAudio');
		if(times > 0) {
			netAudio.play({
				path: resource
			}, function(ret) {
				if(ret.complete) {
					times--;
					cloudApp.netAudioPlay(resource, times);
				}
			});
		}
		return true;
	},

	speechRecognizerRead: function(resource, times) {
		var iflyRecognition = api.require('iflyRecognition');
		iflyRecognition.createUtility({
			android_appid: '5bd167ea',
			ios_appid: '5bd17b48',
		}, function(ret, err) {
			if(ret.status) {
			} else {
				api.alert({ msg: "创建讯飞失败" });
			}
		});
		if(times > 0) {
			iflyRecognition.read({
				readStr: resource,
				speed: 60,
				volume: 100,
				voice: 'xiaoyan',
				rate: 16000
			}, function(ret, err) {
				if (ret.status) {
					if(ret.status) {
						if(ret.speakProgress == 100) {
							times--;
							setTimeout(function() {
								cloudApp.speechRecognizerRead(resource, times);
							}, 1000)
						}
					}
				}
			});
		}
		return true;
	},

	play: function(notify_type, resource, text) {
		var prefs = cloudApp.igetPrefs('phonic');
		if(!prefs) {
			prefs = {
				voice: 1,
				vibrate: 1,
				voice_type: 1,
				new: {
					times: 'loop',
				},
				refund: {
					times: 'loop',
				},
				remind: {
					times: 'loop',
				}
			};
		}
		if(prefs.vibrate) {
			api.notification({
				vibrate: [3000, 3000]
			});
		}
		if(prefs.voice) {
			var times = prefs[notify_type]['times'];
			if(times == 'loop') {
				times = 100;
			} else {
				times = 3;
			}
			var voice_type = prefs['voice_type'];
			if(voice_type == 1) {
				cloudApp.speechRecognizerRead(text, times);
			} else {
				cloudApp.netAudioPlay(resource, times);
			}
		}
		return true;
	},

	initAjpush: function(){
		var ajpush = api.require('ajpush');
		ajpush.setBadge({
			badge:0
		});
		ajpush.init(function(ret) {
			if(ret && ret.status){}
		});
		//同步推送标签和别名
		var push_code = cloudApp.igetPrefs('push_code');
		if(cloudApp.relation && cloudApp.relation.code && (!push_code || (push_code && push_code != cloudApp.relation.code))) {
			var bindParams = {};
			if(cloudApp.relation.alias) {
				bindParams['alias'] = cloudApp.relation.alias;
			}
			if(cloudApp.relation.tags) {
				bindParams['tags'] = cloudApp.relation.tags;
			}
			ajpush.bindAliasAndTags(bindParams, function(ret, err) {
				var statusCode = ret.statusCode;
				if(statusCode == 0) {
					cloudApp.isetPrefs('push_code', cloudApp.relation.code);
				} else {
					alert("同步推送信息出错：" + statusCode);
				}
			});
		}

		ajpush.setListener(function(ret) {
			var extra = ret.extra;
			try {
				extra = JSON.parse(ret.extra);
			} catch(e) {}
			if(api.systemType == 'ios') {
				var ajpush = api.require('ajpush');
				ajpush.setBadge({
					badge:0
				});
			}
			cloudApp.play(extra.notify_type, extra.resource, extra.voice_text);
		});

		api.addEventListener({
			name: 'appintent'
		}, function(ret, err) {
			if(ret && ret.appParam.ajpush) {
				var ajpush = ret.appParam.ajpush;
				try {
					var extra = JSON.parse(ajpush.extra);
				} catch(e) {
					var extra = ajpush.extra;
				}
				location.href = extra.url;
			}
		});

		api.addEventListener({
			name: 'noticeclicked'
		}, function(ret, err) {
			if(ret && ret.value) {
				var ajpush = api.require('ajpush');
				ajpush.setBadge({
					badge:0
				});
				var ajpush = ret.value;
				var extra = ajpush.extra;
				location.href = extra.url;
			}
		});
	},

	igetPrefs: function(key) {
		var keys = key.split('.');
		var data = '';
		if(keys.length == 1) {
			data = api.getPrefs({
				sync: true,
				key: key
			});
			try {
				data = JSON.parse(data);
			} catch(e) {}
		} else if(keys.length == 2) {
			var all = cloudApp.igetPrefs(keys[0]);
			if(all && all[keys[1]]) {
				data = all[keys[1]];
			}
		} else if(keys.length == 3) {
			var all = cloudApp.igetPrefs(keys[0]);
			if(all && all[keys[1]]) {
				data = all[keys[1]][keys[2]];
			}
		}
		return data;
	},

	isetPrefs: function(key, params) {
		var keys = key.split('.');
		if(keys.length == 1) {
			api.setPrefs({
				key: key,
				value: params
			});
		} else if(keys.length == 2) {
			var all = cloudApp.igetPrefs(keys[0]);
			if(!all) {
				all = {};
			}
			all[keys[1]] = params;
			api.setPrefs({
				key: keys[0],
				value: all
			});
		} else if(keys.length == 3) {
			var all = cloudApp.igetPrefs(keys[0]);
			if(!all) {
				all = {};
			}
			if(!all[keys[1]]) {
				all[keys[1]] = {};
			}
			all[keys[1]][keys[2]] = params;
			api.setPrefs({
				key: keys[0],
				value: all
			});
		}
		return true;
	}
};
export default cloudApp


